.app_header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 6rem;
  flex-wrap: wrap;

  @include m-query($tablet) {
    margin: 1rem 0;
  }

  & .logo {
    margin: 0;
    margin-right: 1.5rem;
    color: #fff;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;

    @include m-query($tablet) {
      margin-right: auto;
    }
  }

  & .search_bar {
    flex: 0 0 65%;
    display: flex;
    align-items: center;
    margin-right: auto;
    position: relative;

    &__icon {
      position: absolute;
      width: 15px;
      height: 15px;
      margin-left: 15px;
    }

    &__input {
      width: 100%;
      padding: 12px 15px 12px 30px;
      padding-left: 40px;
      outline: none;
      border: 0;
      border-radius: 32px;
      background-color: var(--white-gray);
    }

    @include m-query($tablet) {
      flex: 0 0 100%;
      order: 2;
    }
  }

  & .login,
  & .signup {
    outline: 0;
    border: 0;
    cursor: pointer;
    text-transform: uppercase;
    background-color: var(--text-color);
    color: var(--white-gray);
    padding: 8px 16px;
    border-radius: 32px;
    display: inline-flex;
    align-items: center;

    &:hover {
      color: var(--text-color);
      background-color: var(--white-gray);
    }
  }
  & .login {
    margin-right: 0.8rem;
  }
}
